    <center>
      <form id="foodInfoForm" on:submit={uploadFoodItem}>
        {#if Food_Image}
          <img
            id="id_Food_Image"
            class="Food_Image w-[20vw] h-[20vw]"
            name="Food_Image"
            src={Food_Image}
